.content {
  position: relative;
  height: 37vw;
  margin-top: -40px; }
  .content__background, .content__background__shadow, .content__background__image, .content__area {
    position: absolute;
    top: 0;
    bottom: 0; }
  .content__background {
    left: 0;
    right: 0; }
    .content__background__shadow {
      left: 0;
      background: #000;
      width: 30%;
      z-index: 2; }
      .content__background__shadow:before {
        content: '';
        position: absolute;
        z-index: 10;
        background-image: linear-gradient(to right, #000, transparent);
        top: 0;
        bottom: 0;
        left: 100%;
        width: 275px; }
    .content__background__image {
      right: 0;
      width: 70%;
      height: 100%;
      background-position: center 10%;
      background-size: cover;
      z-index: 1; }
  .content__area {
    left: 0;
    right: 0;
    height: 100%;
    z-index: 3; }
    .content__area__container {
      padding: 30px 70px;
      color: wheat; }
  .content__title {
    font-size: 45px;
    color: #fff;
    font-weight: 700; }
  .content__description {
    font-size: 18px;
    color: #999;
    margin-top: 20px;
    max-width: 500px; }
  .content__close {
    color: #fff;
    width: 40px;
    height: 40px;
    background: transparent;
    outline: none;
    border: none;
    position: absolute;
    top: 30px;
    right: 30px; }

.slide-button {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 55px;
  background: rgba(0, 0, 0, 0.5);
  border: 0;
  outline: 0;
  padding: 0;
  margin: 40px 0;
  z-index: 4; }
  .slide-button span {
    width: 25px;
    color: #fff;
    display: block;
    margin: 0 auto; }
  .slide-button--next {
    right: 0; }
    .slide-button--next span {
      -webkit-transform: rotateZ(-90deg);
              transform: rotateZ(-90deg); }
  .slide-button--prev {
    left: 0; }
    .slide-button--prev span {
      -webkit-transform: rotateZ(90deg);
              transform: rotateZ(90deg); }

.slider-wrapper {
  padding: 40px 0;
  overflow: hidden;
  position: relative; }

.slider {
  position: relative; }
  .slider__container {
    display: flex;
    padding: 0 55px;
    transition: -webkit-transform 300ms ease 100ms;
    transition: transform 300ms ease 100ms;
    transition: transform 300ms ease 100ms, -webkit-transform 300ms ease 100ms;
    z-index: 3;
    width: 100%; }
  .slider:not(.slider--open) .item:hover .show-details-button {
    opacity: 1; }
  .slider:not(.slider--open) .item:hover {
    -webkit-transform: scale(1.5) !important;
            transform: scale(1.5) !important; }
  .slider:not(.slider--open):hover .item {
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%); }
  .slider:not(.slider--open) .item:hover ~ .item {
    -webkit-transform: translateX(25%);
            transform: translateX(25%); }

.show-details-button {
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  opacity: 0;
  transition: opacity 300ms ease 100ms;
  background: transparent;
  border: 0;
  outline: none;
  width: 100%;
  cursor: pointer; }
  .show-details-button span {
    display: block;
    width: 14px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 auto;
    color: white; }

.markcontent {
  box-sizing: border-box;
  border: solid 4px #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: -4px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
  z-index: 4; }
  .markcontent:before, .markcontent:after {
    position: absolute;
    width: 0;
    height: 0;
    content: "";
    top: 100%;
    left: 50%;
    margin-left: -13px;
    border-style: solid;
    border-width: 7px 13px 0 13px; }
  .markcontent:before {
    border-color: rgba(0, 0, 0, 0.15) transparent transparent transparent;
    margin-top: 5px; }
  .markcontent:after {
    margin-top: 4px;
    border-color: #fff transparent transparent transparent; }

.item {
